export const FETCH_COUNTRIES_BEGIN = 'FETCH_COUNTRIES_BEGIN'
export const FETCH_COUNTRIES_SUCCESS = 'FETCH_COUNTRIES_SUCCESS'
export const FETCH_COUNTRIES_FAILURE = 'FETCH_COUNTRIES_FAILURE'

export const POST_SELECTED_COUNTRIES_BEGIN = 'POST_SELECTED_COUNTRIES_BEGIN'
export const POST_SELECTED_COUNTRIES_SUCCESS = 'POST_SELECTED_COUNTRIES_SUCCESS'
export const POST_SELECTED_COUNTRIES_FAILURE = 'POST_SELECTED_COUNTRIES_FAILURE'


const fetchCountriesBegin = () => ({
  type: FETCH_COUNTRIES_BEGIN
})

const fetchCountriesSuccess = (countries) => ({
  type: FETCH_COUNTRIES_SUCCESS,
  payload: { countries }
})

const fetchCountriesFailure = (error) => ({
  type: FETCH_COUNTRIES_FAILURE,
  payload: { error }
})

export const fetchCountries = () => {
  let tokenBearer = 'Bearer '.concat(localStorage.getItem('token'))
  console.log(tokenBearer)
  return (dispatch) => {
    dispatch(fetchCountriesBegin())
    return fetch('http://localhost:8080/api/v1/countries', {      
      headers: {'Authorization': tokenBearer}
    })
      .then(handleErrors)
      .then((response) => {
        return response.json()
      })
      .then((data) => {
        dispatch(fetchCountriesSuccess(data))
        return data
      })
      .catch(error => dispatch(fetchCountriesFailure(error)))
  }
}

// Handle HTTP errors since fetch won't.
const handleErrors = (response) =>{
  if (!response.ok) {
    throw Error(response.statusText)
  }
  return response
}


const postSelectedCountriesBegin = () => ({
  type: POST_SELECTED_COUNTRIES_BEGIN
})

const postSelectedCountriesSuccess = (selectedCountries) => ({
  type: POST_SELECTED_COUNTRIES_SUCCESS,
  payload: {selectedCountries}
})

const postSelectedCountriesFailure = (error) => ({
  type: POST_SELECTED_COUNTRIES_FAILURE,
  payload: {error}
})

export const postSelectedCountries = (userId, selectedCountries) => {
  let tokenBearer = 'Bearer '.concat(localStorage.getItem('token'))  
  return (dispatch) => {
    dispatch(postSelectedCountriesBegin())
    return fetch('http://localhost:8080/api/v1/users/'+ userId + '/countries',
    {method: 'post',
     headers: {'Authorization': tokenBearer,
    'Content-Type': 'application/json'},
     body: JSON.stringify({
       countryIdsList: selectedCountries
     })
   }).then(handleErrors)
   .then(response => {return response.json()})
   .then(data => {
     dispatch(postSelectedCountriesSuccess(data))
     return data
   })
   .catch(error => dispatch(postSelectedCountriesFailure(error)))
  }
}





