import React, { Component } from 'react'
import { connect } from 'react-redux'
import { push } from 'react-router-redux'

import logo from '../logo.png'
import styles from './AppNavbar.scss'


class AppNavbar extends Component {


  logOut() {
    localStorage.clear()
    this.props.navigateTo('/')
  }


    render() {
        return (
          <nav className={styles.navbar}>
              <div className="container-fluid">
                  <div className={styles.header} >
                      <a className={styles.brand}>
                          <img alt="logo" src={logo} />
                      </a>
                  </div>
                  <ul className={`nav navbar-nav navbar-right`}>
                      <li><a><span className={`glyphicon ${styles.btn}`}></span>Home</a></li>
                      <li><a><span className={`glyphicon ${styles.btn}`}></span>Profile</a></li>
                      <li><a onClick={() => this.logOut()}><span className={`glyphicon glyphicon-log-out`}></span> Logout</a></li>
                  </ul>
              </div>
          </nav>
        )
    }
}

const mapDispatchToProps = (dispatch) => ({
  navigateTo: (e) => dispatch(push(e))
})

export default connect(null, mapDispatchToProps)(AppNavbar)



