import {
  FETCH_COUNTRIES_BEGIN,
  FETCH_COUNTRIES_SUCCESS,
  FETCH_COUNTRIES_FAILURE,

  POST_SELECTED_COUNTRIES_BEGIN,
  POST_SELECTED_COUNTRIES_SUCCESS,
  POST_SELECTED_COUNTRIES_FAILURE
} from '../actions/countryActions'


const initialCountriesState = {
    items: [],
    loading: false,
    error: null
}

export function fetchCountriesReducer(state = initialCountriesState,
  action) {
  switch(action.type) {

    case FETCH_COUNTRIES_BEGIN:

      return {
        ...state,
        loading: true,
        error: null
      }

    case FETCH_COUNTRIES_SUCCESS:

      return {
        ...state,
        loading: false,
        items: action.payload.countries,
      }

    case FETCH_COUNTRIES_FAILURE:

      return {
        ...state,
        loading: false,
        error: action.payload.error,
        items: []
      }

    default:
      return {...state}
  }
}


const postCountriesInitialState = {
     loading: false,
     error: false,
     selectedCountries: []
   }

export const postCountriesReducer = (state=postCountriesInitialState, action) => {

    switch(action.type) {
       case POST_SELECTED_COUNTRIES_BEGIN:
          return {
              ...state,
              loading: true
          }
        case POST_SELECTED_COUNTRIES_SUCCESS:
           return {
              ...state,
              loading: false,
              selectedCountries: action.payload.selectedCountries
            }
        case POST_SELECTED_COUNTRIES_FAILURE:
            return {
              ...state,
              error: true
            }
        default:
            return {
              ...state
            }
       }

   }



