import React, { Component } from 'react'
import { connect } from 'react-redux'
import { push } from 'react-router-redux'

import Input from '../input/Input'
import Password from '../password/Password'

import {
  validateUserName,
  validateEmail,
  validatePassword
} from '../../../actions/validationActions'
import {authorizeUser} from '../../../actions/authActions' 
import { createUser } from '../../../actions/signupActions'
import loading from '../loading.gif'

import styles from './Signup.scss'

class Signup extends Component {

  redirect() {
    if (this.props.signUpSuccessful)    
      this.props.navigateTo('/app')
    else if (this.props.isLoading) {
      return <img src={loading} alt='loading' />
    } else if (this.props.signUpError) {
      return <p>Error</p>
    }
  }

  render() {

    return (
      <div className={styles.div}>
        <form className={styles.form}>
          <br />
          <Input type="username" className={styles.userName} isLoading={this.props.isLoadingUserName} isValid={this.props.isValidUserName} validate={this.props.validateUserName.bind(this)} />
          <br />
          <Input type="email" className={styles.email} isLoading={this.props.isLoadingEmail} isValid={this.props.isValidEmail} validate={this.props.validateEmail.bind(this)} />
          <br />
          <Password className={styles.password} isLoading={this.props.isLoadingPassword} passwordStrength={this.props.passwordStrength} validate={this.props.validatePassword.bind(this)} />
          <br />
          <button className={`btn btn-block ${styles.button}`} type="submit" onClick={(e) => { e.preventDefault(); this.props.createUser(this.props.userName, this.props.email, this.props.password) }}>
            Sign Up
          </button>
          {this.redirect()}
        </form>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    isValidUserName: state.userValidation.userName.isValid,
    isLoadingUserName: state.userValidation.userName.loading,

    isValidEmail: state.userValidation.email.isValid,
    isLoadingEmail: state.userValidation.email.loading,

    passwordStrength: state.userValidation.password.passwordStrength,
    isLoadingPassword: state.userValidation.password.loading,

    userName: state.userValidation.userName.value,
    email: state.userValidation.email.value,
    password: state.userValidation.password.value,

    signUpLoading: state.user.signUp.loading,
    signUpError: state.user.signUp.error,
    signUpSuccessful: state.user.signUp.successful
  }
}

const mapDispatchToProps = (dispatch) => ({
  validateUserName: (e) => dispatch(validateUserName(e)),
  validateEmail: (e) => dispatch(validateEmail(e)),
  validatePassword: (e) => dispatch(validatePassword(e)),
  createUser: (userName, email, password) => dispatch(createUser(userName, email, password)),
  authorizeUser: () => dispatch(authorizeUser()),
  navigateTo: (e) => dispatch(push(e))
})

export default connect(mapStateToProps, mapDispatchToProps)(Signup)



