import thunk from 'redux-thunk'
import logger from 'redux-logger'
import { createStore,  combineReducers, applyMiddleware } from 'redux'
import {routerMiddleware, routerReducer } from 'react-router-redux'
import { composeWithDevTools } from 'redux-devtools-extension'
// import { persistReducer } from 'redux-persist'
// import storage from 'redux-persist/lib/storage'

import {countryPickerReducer} from './reducers/countryPickerReducer'
import {fetchCountriesReducer,
  postCountriesReducer} from './reducers/countryReducer'

import {validationReducer} from './reducers/validationReducer'
import {signupReducer} from './reducers/signupReducer'
import {authReducer} from './reducers/authReducer'

import myHistory from './history'

const routingMiddleware = routerMiddleware(myHistory)

const reducers = combineReducers({
    fetchCountries: fetchCountriesReducer,
    countryPicker: countryPickerReducer,
    postCountries: postCountriesReducer,
    user: signupReducer,
    userValidation: validationReducer,
    router: routerReducer,
    auth: authReducer
  })

// const persistConfig = {
//     key: 'root',
//     storage,
//     whitelist: ['user.userId'],
// }

// const persistedReducer = persistReducer(persistConfig, reducers)

const myStore = createStore(reducers,
    composeWithDevTools(applyMiddleware(logger, thunk, routingMiddleware)))

export default myStore



