export const ADD_COUNTRY_PICKER = 'ADD_COUNTRY_PICKER'
export const REMOVE_COUNTRY_PICKER = 'REMOVE_COUNTRY_PICKER'
export const SELECT_COUNTRY_PICKER = 'SELECT_COUNTRY_PICKER'



export const addCountryPicker = () => ({
    type: ADD_COUNTRY_PICKER
  })
  
  
  export const removeCountryPicker = (countryPickerId) => ({
    type: REMOVE_COUNTRY_PICKER,
    payload: {countryPickerId}
  })
  
  export const selectCountry = (countryPickerId, selectedCountryId) => ({
    type: SELECT_COUNTRY_PICKER,
    payload: {countryPickerId, selectedCountryId}
  })


