export const CREATE_USER_BEGIN = 'POST_USER_CREDENTIALS_BEGIN'
export const CREATE_USER_SUCCESS = 'POST_USER_CREDENTIALS_SUCCESS'
export const CREATE_USER_FAILURE = 'POST_USER_CREDENTIALS_FAILURE'

const handleErrors = (response) =>{
  if (!response.ok) {
    throw Error(response.statusText)
  }
  return response
}


const createUserbegin = () => ({
  type: CREATE_USER_BEGIN
})

const createUserSuccess = (userId) => ({
  type: CREATE_USER_SUCCESS,
  payload: { userId }
})

const createUserFailure = () => ({
  type: CREATE_USER_FAILURE
})

export const createUser = (userName, email, password) => {
  return (dispatch) => {
    dispatch(createUserbegin())
    fetch('http://localhost:8080/tokens/signup', {
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        userName: userName,
        email: email,
        password: password
        })
      })
      .then(handleErrors)
      .then((response) => response.json())
      .then((data) => {
        dispatch(createUserSuccess(data.userId))
        localStorage.setItem('token', data.token)        
        return data
      })
      .catch((error) => dispatch(createUserFailure()) )
  }
}



