import React, { Component } from 'react'
import Signup from '../auth/signup/Signup'
import styles from './Home.scss'
import img from './home.jpg'
import HomeNavbar from '../navigation/home/HomeNavbar'

import { authorizeUser } from '../../actions/authActions'
import { push } from 'react-router-redux'
import { connect } from 'react-redux'

class Home extends Component {

    render() {

        return (
            <div>
                <HomeNavbar />
                <div className={styles.header}>
                    <img className={styles.img} src={img} alt="Home Img" />
                    <div>
                        <div className={styles.introduction}>
                            <p className={styles.largeText}>
                                Travelneer
                            </p>
                            <p className={styles.mediumText}>
                                A Traveler's Companion
                            </p>
                            <ul className={styles.smallText}>
                                <li>
                                    Meet other Travelerneers
                                </li>
                                <li>
                                    Share your Adventures
                                </li>
                            </ul>
                        </div>
                        <Signup />
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    isAuthorized: state.auth.auth.isAuthorized
})

const mapDispatchToProps = (dispatch) => ({
    authorizeUser: () => dispatch(authorizeUser()),
    navigateTo: (e) => dispatch(push(e))
})

export default connect(mapStateToProps, mapDispatchToProps)(Home)



