
import {
  ADD_COUNTRY_PICKER,
  REMOVE_COUNTRY_PICKER,
  SELECT_COUNTRY_PICKER
}
  from '../actions/countryPickerActions'

const initialCountryPickersState = {
  countryPickers: []
}

export function countryPickerReducer(state = initialCountryPickersState,
  action) {
  switch (action.type) {

    case ADD_COUNTRY_PICKER:

      const countryPickers = [...state.countryPickers,
      { selectedCountryId: "" }]

      return {
        ...state,
        countryPickers: countryPickers
      }

    case REMOVE_COUNTRY_PICKER:

      let countryPickersRemove = [...state.countryPickers]
      countryPickersRemove.splice(action.payload.countryPickerId, 1)

      return {
        ...state,
        countryPickers: countryPickersRemove

      }

    case SELECT_COUNTRY_PICKER:

      let countryPickersSelect = [...state.countryPickers]
      countryPickersSelect[action.payload.countryPickerId] = { selectedCountryId: action.payload.selectedCountryId }

      return {
        ...state,
        countryPickers: countryPickersSelect
      }

    default:
      return {
        ...state
      }
  }
}


